function DetailView(props) {

    let { isbn } = useParams();
    let book = props.books && props.books.find(x => x.isbn == isbn);

    return (
        <div>
            <img src={book.coverLargeUrl} width="300rem"/>
            {book.title}<br/>
            {book.author}<br/>
            {book.translator}<br/>
            {book.pubDate}<br/>
            {book.publisher}<br/>
            {book.categoryId}<br/>
            {book.categoryName}<br/>
            {book.isbn}<br/>
            {book.customerReviewRank}<br/>
            {book.description}<br/>
            <RentButton/>
        </div>
    )
}

